


































































































import Vue from 'vue'

import {
  displayItem,
} from './scene_tiles'

import {
  LeftHandSlot,
  RightHandSlot,
  BodySlot,
  MissileSlot,

  Patrol,
  Explorer,
  Waiter,
  Chaser,
  Escaper,
  Loiter,
  Dispatcher,
  Picker,
  SelfHealer,
  Attacker,
  Thrower,
} from '../src/engine'

export default Vue.extend({
  name: 'Stats',
  props: ['creature'],
  computed: {
  },
  methods: {
    aiName(ai) {
      if (ai instanceof Patrol) {
        return 'Patrol'
      }
      if (ai instanceof Escaper) {
        return 'Escaper'
      }
      if (ai instanceof Chaser) {
        return 'Chaser'
      }
      if (ai instanceof Explorer) {
        return 'Explorer'
      }
      if (ai instanceof Waiter) {
        return 'Waiter'
      }
      if (ai instanceof Loiter) {
        return `Loiter`
      }
      if (ai instanceof Dispatcher) {
        return `Dispatcher (${this.aiName(ai.aiToRun)})`
      }
      if (ai instanceof Attacker) {
        return `Attacker`
      }
      if (ai instanceof SelfHealer) {
        return `SelfHealer`
      }
      if (ai instanceof Picker) {
        return 'Picker'
      }
      if (ai instanceof Thrower) {
        return 'Thrower'
      }
    },
    displayItem(item) {
      if (item) {
        return `${displayItem(item).getChar()} ${item.name}`
      }
    },
    displayAttribute(attribute) {
      if (attribute.currentValue() != attribute.maximum()) {
        return `${attribute.currentValue()} [${attribute.maximum()}]`
      } else {
        return attribute.currentValue()
      }
    },
    displayAttributeWithModifiers(attribute) {
      if (attribute.currentValue() != attribute.maximum()) {
        const mods = attribute.modifiers.map(x => x > 0 ? `+ ${x}` : `- ${x}`).join(' ')
        return `${attribute.currentValue()} = [${attribute.maximum()}] ${mods}`
      } else {
        return attribute.currentValue()
      }
    },
    displayBodyPart(bodyPart) {
      switch (bodyPart.id) {
        case LeftHandSlot.id:
          return 'Левая рука'
        case RightHandSlot.id:
          return 'Правая рука'
        // case BodyPart.Legs:
        //   return '足'
        // case BodyPart.Finger:
        //   return '指'
        // case BodyPart.Head:
        //   return '頭'
        // case BodyPart.Eye:
        //   return '目'
        // case BodyPart.Neck:
        //   return '首'
        // case BodyPart.Back:
        //   return '背'
        case BodySlot.id:
          return 'Корпус'
        // case BodyPart.MissileWeapon:
        //   return 'MissileWeapon'
        case MissileSlot.id:
          return 'Снаряды'
        default:
          throw `Unknow body part ${bodyPart}`
      }
    }
  }
})
